<template>
  <section>    
    <div class="container py-6 px-2 m-auto">    
        
        <div class="flex flex-1 flex-col w-full border shadow rounded-lg bg-white text-left py-4 p-8"> 
          <tabs>
            <tab name="Informacion General">              
              <form action="" class="mt-2" 
              @submit="submitForm">                         
                  <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Compañía</label>
                          <span>{{ company }}</span>
                          <span class="text-white bg-orange rounded-xl ml-auto p-1 px-2" 
                          v-show="(msg != '')">
                            <i class="fa fa-exclamation-triangle"></i>
                            {{ msg }}
                          </span>
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">IATA</label>
                          <span>{{ IATA }}</span>
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombres *</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.first_name" required>
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Apellidos *</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.last_name" required>
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Dirección</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.address">
                      </div>
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Genero *</label>
                          <Multiselect
                            v-model="formData.gender" 
                            mode="single"
                            :searchable="true"                                               
                            :options="genders"
                            placeholder="Seleccione genero *"      
                            :classes="select_style_opt"
                            />
                          <!-- <model-select :options="genders"
                                  v-model="formData.gender"
                                  placeholder="Seleccione genero *"
                                  required
                                  class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                  focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                          </model-select>                    -->
                      </div>                                                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">EMail *</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                          readonly
                          v-model="formData.email" required>
                      </div>
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Teléfono</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.phone">
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Ciudad</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.city">
                      </div>
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Código Postal</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.zip_code">
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">País *</label>
                          <Multiselect
                            v-model="formData.country" 
                            mode="single"
                            :searchable="true"                                               
                            :options="countries"
                            placeholder="Seleccione País *"      
                            :classes="select_style_opt"
                            />
                          <!-- <model-select :options="countries"
                                  v-model="formData.country"
                                  placeholder="Seleccione País *"
                                  required
                                  class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                  focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                          </model-select>                    -->
                      </div>                
                      <div class="flex flex-row items-center my-3 mx-2">
                          <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Provincia</label>
                          <input type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                          v-model="formData.province">
                      </div>                                      
                      <!-- PASSWORD RESET OPTIONS -->
                      <div class="flex flex-col border border-gray-400 rounded-md p-5 mt-3">
                          <div class="flex flex-row gap-x-3 items-center my-3 mx-2">
                              <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Contraseña</label>
                              <input 
                              :type="(showpass) ? 'text' : 'password'" 
                              class="flex-1 border mt-1 block w-90x100 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                              v-model="pass">
                              <label class="uppercase block text-sm font-medium text-gray-700 mx-2 ">Confirmar Contraseña</label>
                              <input 
                              :type="(showpass) ? 'text' : 'password'" 
                              class="flex-1 border mt-1 block w-full border-gray-400 bg-white rounded-md shadow-sm py-2 px-3
                              focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "
                              v-model="repassword">
                              <div class="flex items-center gap-x-4">                    
                                  <button type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                                  <button type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>                                                
                              </div>                        
                          </div>    
                          <div class="flex justify-between">
                              <small class="text-red" v-show="!samepass">Las contraseñas deben ser iguales.</small>               
                              <!-- <small class="text-green" v-show="samepass">Las contraseñas deben ser iguales.</small>                -->
                              <button type="button" class="ml-auto">Resetear Contraseña</button>
                          </div>
                      </div>                

                  </div>            

                  <!-- Button -->
                  <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                      <button type="submit" class="inline-flex border border-transparent h-10 bg-orange rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                          hover:bg-amber-300 
                          focus:outline-none                           
                          active:bg-green-600 
                          disabled:opacity-25 transition">Salvar</button>            
                          <button type="button" class="inline-flex border border-gray-400 h-10 bg-indigo-900 rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                          hover:text-white hover:bg-indigo-400 
                          focus:outline-none
                          active:text-gray-800 active:bg-indigo-900 
                          disabled:opacity-25 transition">Cancelar</button> 
                      <div v-if="error_message" class="error-message">

                          <i class="fa fa-times-circle"></i>
                          <h5 class="text-xl self-center px-2">
                            Error enviando informacion. No se pudo crear el nuevo usuario
                            <p>{{error_message_text}}</p>
                          </h5>
                        </div>
                        <div v-if="success_message" class="success-message">
                          <i class="fa fa-check-circle"></i>
                          <h5 class="text-xl self-center px-2">La información ha sido enviada satisfactoriamente.</h5>
                      </div>
                  </div> 
              </form>
            </tab>
            <tab name="Compradores" v-if="isAgent">
              <new-vendor 
              :countries="countries" 
              :genders="genders"
              :vendorId="vendor"
              @clearId="vendor=''"></new-vendor>              
              <div class="flex flex-wrap bg-backBoard rounded-xl p-4 shadow-inner gap-4">
                <template v-if="vendors.length > 0">
                  <Vendor v-for="item in vendors" :key="item.id" 
                  :item="item"
                  @updVendor="updVendor"
                  @delVendor="delVendor"
                   />
                </template>
                <span v-if="vendors.length == 0">No tiene ningun vendedor asignado</span>
              </div>
            </tab>
          </tabs>                  
        </div>
        
    </div>
  </section>  
</template>

<script>
import Vendor from '@/components/Admin/Vendor';
// import '@/assets/css/table.css'

// External services
import Authorization from "@/services/user/auth.service";
import GenderService from "@/services/common/gender.service";
import CountryService from "@/services/common/country.service";
import ClientService from "@/services/user/client.service";

// Component
import NewVendor from '@/components/Admin/NewVendor'
import Multiselect from '@vueform/multiselect'
import '@vueform/multiselect/themes/default.css'
import {Tabs, Tab} from 'vue3-tabs-component'
import '@/assets/css/tabs.css'

export default {
  name: "AdminComp",  
  components: {
    Vendor,
    Tabs, Tab,
    Multiselect,
    NewVendor
  },  
  data() {
    return { 
      title: 'Client',
      mode: 'modify',
      vendor: '',
      error_message_text: '',
      error_message: false,
      msg: '',
      success_message: false,
      select_style_opt: {
        container: 'relative w-4/12 flex items-center justify-end box-border cursor-pointer border border-[#9CA3AF] rounded-lg bg-white text-sm leading-snug outline-none',
        search: 'w-4/12 absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-sm font-sans bg-white rounded-lg pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
        optionSelected: 'text-white bg-lightGray',
        optionSelectedPointed: 'text-white bg-lightGray opacity-90',
        optionSelectedDisabled: 'text-green-100 bg-lightGray bg-opacity-50 cursor-not-allowed',
      },
      showpass: false,
      samepass: true,
      pass: '',
      repassword: '',
      vendors: [],
      countries: [],
      genders: [],
      company: '',
      IATA: '',
      formData: {
          from_web: true,
        //general info
          first_name: '',
          last_name: '',
          phone: '',
          email: '',          
          gender_id: '',
          gender: {
              value: '',
              label: ''
          },
          password: '',                
        //location
          address: '',
          city: '',
          province: '',
          zip_code: '',
        //agency                    
          country_id: '',
          country: {
              value: '',
              label: ''
          },
      },
    }
  },  
  created() {     
    if(!Authorization.isLogged()) 
      this.$router.push('/')
    else {
      this.msg = localStorage.getItem('user_msg')
      let data =[]
      data.push(CountryService.getSimpleList())//.then(c => this.parents = c)
      data.push(GenderService.getSimpleList())//.then(c => this.parents = c)
      data.push(ClientService.getVendors())//.then(c => this.parents = c)
      Promise.all(data).then(d => {
          this.countries = d[0]
          this.genders = d[1]        
          this.vendors = d[2]        
          this.initForm()
      })              
    }
  },
  watch: {
    repassword() {        
      this.samepass = (this.repassword == this.pass || (this.repassword == '' && this.pass == ''))
    }      
  },
  computed: {
    //Valido que el role sea agente
    isAgent() {
      return Authorization.validateRole('ROLE_Agency')
    }
  },
  methods: { 
    initForm() {
            // For upd action
            const client_id = localStorage.getItem('id')
            ClientService.getClient(client_id).then(e => {                
                this.formData.first_name = e.first_name
                this.formData.last_name = e.last_name
                this.formData.phone = e.phone
                this.formData.email = e.email                    
                this.formData.address = e.address
                this.formData.city = e.city
                this.formData.province = e.province
                this.formData.zip_code = e.zip_code                    
                this.formData.gender_id = e.gender_id
                this.formData.gender = e.gender_id
                this.formData.country_id = e.country_id
                this.formData.country = e.country_id                                                            
                this.company = e.company
                this.IATA = e.i_a_t_a
            })
        },   
    submitForm(e) {
        e.preventDefault();    
        this.formData.password = this.pass                            
        this.formData.gender_id = this.formData.gender
        this.formData.country_id = this.formData.country
        ClientService.addUpdClient(this.formData, false).then(res => {                    
            if(res) {                
                this.error_message = false
                this.success_message = true
                setTimeout(() => {
                    this.success_message = false
                }, 3000); 
            }                    
        })
    },    
    updVendor(id) {
      this.vendor = id
    },
    delVendor(id) {
      ClientService.delete(id).then(v => this.vendors = v)
    }
  },
  
};
</script>

<style scoped>
section {
  min-height: 1000px;
}
</style>
