import Authorization from "../user/auth.service";

const genderservice = {
    getList() {
        try {
            return Authorization.checkLogin(`/gender`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        return this.getList().then(list => {
            const result = []
            for (let l of list) {
                result.push({
                    value: l.id,
                    label: l.alias
                })
            }
            return result
        })

    },
    async getGender(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/gender/${id}`)
            if (status == 200) {
                const result = data
                return result
            }
        } catch (e) {
            console.log(e.response)
        }
    },
    async addUpdGender(gender_data, is_new = true) {
        try {
            //console.log(gender_data)
            gender_data.auth = 'bearer'
            const { status, data } = (is_new) ?
            await Authorization.checkLogin('/gender/create', gender_data, 'post'):
                await Authorization.checkLogin(`/gender/upd/${gender_data.id}`, gender_data, 'put')
            if (status == 200) {
                return data
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
        return false
    },
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status } = await Authorization.checkLogin('/gender/delete', delete_data, 'post')
            if (status == 200) {
                // return data
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default genderservice