import Authorization from "../user/auth.service";

const ProductTypeService = {
    getList() {
        try {
            return Authorization.checkLogin(`/product-type`).then(res => {
                const list = []
                if (res.status == 200) {
                    for (let item of res.data) {
                        list.push({
                            id: item.id,
                            alias: item.alias,
                            active: (item.active) ? true : false,
                            /* order: item.order,
                            combination_discount: item.combination_discount,
                            travel_agent_margin: item.travel_agent_discount,
                            book_more_than_one_discount: item.book_more_than_one_discount,
                            name_class: item.name_class,
                            id_pyth: item.id_pyth,
                            release: item.release,
                            margin: item.margin,
                            show_in_newsletter: item.show_in_newsletter,
                            pax_info: item.pax_info,
                            db_table_name: item.db_table_name,
                            accept_on_request: item.accept_on_request,
                            accept_payment: item.accept_payment, */
                        })
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
                return list
            })
        } catch (e) {
            console.log(e)
        }
    },
    getSimpleList() {
        try {
            return Authorization.checkLogin(`/product-type`).then(res => {
                if (res.status == 200) {
                    const producttypes = []
                    for (let item of res.data) {
                        producttypes.push({
                            value: item.id,
                            label: item.alias
                        })
                    }
                    return producttypes
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    getCustomList() {
        try {
            return Authorization.checkLogin(`/product-type`).then(res => {
                if (res.status == 200) {
                    const producttypes = []
                    for (let item of res.data) {
                        if(item.active == 1) {
                            producttypes.push({
                                id: item.id,
                                name: item.alias,
                                class_name: item.class_name,
                                agent_margin: item.travel_agent_margin
                            })
                        }
                    }
                    return producttypes
                }
            })
        } catch (e) {
            console.log(e)
            return []
        }
    },
    async getProductType(id) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/product-type/${id}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    },    
    async delete(ids) {
        const delete_data = {
            auth: 'bearer',
            ids
        }
        try {
            const { status } = await Authorization.checkLogin('/product-type/delete', delete_data, 'post')
            if (status == 200) {
                return this.getList().then(list => {
                    return list
                })
            }
        } catch (e) {
            //! Fail 
            console.log(e.response)
        }
    }
}

export default ProductTypeService