<template>
    <article :id="getValue('id')" class="flex  gap-6 w-[32%] bg-white rounded-xl px-6 py-4">
        <i class="fa fa-user-circle text-gray-300 text-[6rem]"></i>
        <div class="text-left">
            <h3 class="text-xl font-bold">{{ getValue('name') }}</h3>         
            <span>{{ getValue('email') }}</span>
            <div class="flex gap-6 py-4">                
                <span class="flex items-center gap-1">
                    <i class="text-gray cursor-pointer" :class="isActive(getValue('active'))"
                    @click="updActive(getValue('id'))"></i> 
                    Activo
                </span>
                <span class="flex items-center gap-1">
                    <i class="text-gray cursor-pointer" :class="isActive(getValue('verified'))"
                    @click="updVerify(getValue('id'))"></i>
                     Verificado
                </span>
            </div>   
        </div>
        <div class="flex flex-col h-full gap-6 self-center ml-auto">
            <i class="fa fa-times-circle text-2xl text-red cursor-pointer" @click="deleteVendor(getValue('id'))"></i>
            <i class="fa fa-edit text-2xl text-blue cursor-pointer mt-auto" @click="updateVendor(getValue('id'))"></i>
        </div>
    </article>
</template>
<script>
import ClientService from "@/services/user/client.service";

export default {
    name: 'TableComp',
    props: {
        item: {
            default: {}
        }
    },    
    data() {
        return {
            clientInfo: {}
        }
    },        
    methods: {
        isActive(value) {
            return (value > 0) ? 'fa-solid fa-square-check' : 'fa-solid fa-square'
        },
        getValue(field) {
            if(!this.clientInfo[field])
                this.clientInfo[field] = this.item[field]
            return this.clientInfo[field]
        },
        updateVendor(vendorId) {            
            this.$emit('updVendor', vendorId)
        },
        deleteVendor(vendorId) {
            this.$emit('delVendor', vendorId)
        },
        async updActive(vendorId) {
            try {
                const client = await ClientService.addUpdVendor({ id: vendorId, active: !this.clientInfo.active })
                if(client) {
                    this.clientInfo.active = client.active
                }
            } catch(err) {
                console.log(err)
            }
        },
        async updVerify(vendorId) {
            try {
                const client = await ClientService.addUpdVendor({ id: vendorId, verified: !this.clientInfo.verified })
                if(client) {
                    this.clientInfo.verified = client.verified
                }
            } catch(err) {
                console.log(err)
            }
        },
    }
}
</script>
